<template>
  <div id="app">
    <el-container style="heigth:100%; direction:vertical">
      <el-header height="45px">
        <div class="header">
          <div class="menu" >
              <i v-if="isBack" class="el-icon-back head-icon" @click="goBack"></i>
              <i v-else class="el-icon-menu head-icon"></i>
          </div>
          <div class="title">{{msg}}</div>
        </div>
      </el-header>
      <el-main style="width:100%">
        <router-view style="width:100%" v-on:change-page="pageChange"/>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      msg: "实时热点"
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    pageChange(str){
      if(str === 'news-item'){
        this.msg = '热点详情'
      }
    },
  },
  computed: {
    isBack() {
      console.log(this.$route.path)
      return ["/"].indexOf(this.$route.path) < 0;
    }
  },
  watch:{
    '$route'(to,from){
      if(to.name === 'NewsList'){
        this.msg = "实时热点"
      }
    }
  }
};
</script>

<style scoped>
.el-header {
  padding: 0;
  background-color: #b3c0d1;
  color: #333;
  line-height: 45px;
}
.el-main {
  padding: 0;
}
.el-col {
  border-radius: 4px;
}
.head_title {
  text-align: center;
}
.header {
  display: flex;
  align-items: center;
}
.menu {
  text-align: center;
  width: 10%;
}
.title {
  text-align: center;
  width: 90%;
}
</style>



